/*FOOTER*/

footer {
  background: #16222A;
  /* background: -webkit-linear-gradient(59deg, #3A6073, #16222A); */
  background: linear-gradient(59deg, #3A6073, #16222A);
  /* color: white; */
  /* margin-top:100px; */
}

footer a {
  color: #fff;
  font-size: 13px;
  transition-duration: 0.2s;
}

.footer-nosotros {
  color: #fff;
  font-size: 13px;
  transition-duration: 0.2s;
}

.footer-ubicacion 
{
  color: #fff;
  font-size: 13px;
  transition-duration: 0.2s;
  text-align: left;
  margin-left: 7rem;
}

.footer-contacto
{
  color: #fff;
  font-size: 24px;
  transition-duration: 0.2s;
  text-align: left;
  margin-left: 10rem;
  padding-bottom: 4rem;
}

.footer-icono-ubicacion
{
  margin-left: -25px;
}

.footer-icono-contacto
{
  margin-left: -55px;  
}

footer a:hover {
  color: #FA944B;
  text-decoration: none;
}

footer p:hover {
  color: #FA944B;
  text-decoration: none;
}

.copy {
  font-size: 12px;
  padding: 10px;
  border-top: 1px solid #FFFFFF;
}

.footer-middle {
  padding-top: 2em;
  color: white;
}


/*SOCİAL İCONS*/

/* footer social icons */

ul.social-network {
  list-style: none;
  display: inline;
  margin-left: 0 !important;
  padding: 0;
}

ul.social-network li {
  display: inline;
  margin: 0 5px;
}


/* footer social icons */
.social-network a.icoFacebook:hover {  
  background-color: #3B5998;
}

.social-network a.icoLinkedin:hover {
  background-color: #007bb7;
}

.social-network a.icoFacebook:hover i,
.social-network a.icoLinkedin:hover i {
  color: #fff;
}

.social-network a.socialIcon:hover,
.socialHoverClass {
  color: #44BCDD;
}

.social-circle li a {
  display: inline-block;
  position: relative;
  margin: 0 auto 0 auto;
  border-radius: 50%; 
  text-align: center;
  width: 30px;
  height: 30px;
  font-size: 15px;  
}

.social-circle li i {
  margin: 0;
  line-height: 30px;
  text-align: center;
  
}

.social-circle li a:hover i,
.triggeredHover {
  transform: rotate(360deg);
  /* -moz-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -ms--transform: rotate(360deg); */

  /* -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s; */
  transition: all 0.2s;
}

.social-circle i {
  color: #595959;
  /* -webkit-transition: all 0.8s;
  -moz-transition: all 0.8s;
  -o-transition: all 0.8s;
  -ms-transition: all 0.8s; */
  transition: all 0.8s; 
}

.social-network a {
  /* background-color: #F9F9F9; */

  color: #F9F9F9;
  border-radius: 50%;
  text-align: center;
}

